:root {
  --dangerus-color: #dc3545;
  --success-color: #3d7edf;
  --background-color: #c2cee8;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --table-header-bg: #4a5568;
  --table-header-color: white;
  --table-row-hover: #f1f1f1;
  --table-data-bg: white;
  --table-border-color: #ddd;
  --btn-bg: #7e8aa1;
  --btn-hover-bg: #5b677a;
  --btn-active-bg: #3b4656;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.App {
  background-color: var(--background-color);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.result-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  min-height: 100vh;
  max-width: 100%;
  background-color: var(--background-color);
  box-shadow: 0px 4px 8px var(--shadow-color);
}

.input-field {
  border-color: var(--dangerus-color);
}

.input-valid {
  border-color: var(--success-color);
}

.text-danger {
  color: var(--dangerus-color);
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.submit-btn, .btn-back {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.submit-btn {
  background-color: var(--success-color);
}

.btn-back {
  background-color: var(--btn-bg);
  color: white;
}

.btn-back:hover {
  background-color: var(--btn-hover-bg);
  box-shadow: 0px 4px 12px var(--shadow-color);
}

.btn-back:active {
  background-color: var(--btn-active-bg);
  box-shadow: none;
}

.table {
  width: 50%;
  max-width: 100vw;
  border-collapse: collapse;
  margin-top: 20px;
  box-shadow: 0px 4px 8px var(--shadow-color);
  border-radius: 10px;
  overflow: hidden;
}

.table th, .table td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid var(--table-border-color);
  background-color: var(--table-data-bg);
  color: #333;
}

.table th {
  background-color: var(--table-header-bg);
  color: var(--table-header-color);
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.table tr:hover {
  background-color: var(--table-row-hover);
}

.table tr:last-child td {
  border-bottom: none;
}